<script>
import Header from "../../components/global/header/HeaderArea.vue"
import { ref, onBeforeMount } from 'vue'
import { useRouter } from "vue-router"
import { Toast, ToastConfirm } from '@/components/global/swal'
import { apiGetServiceInfo, apiDeleteServiceInfo } from "@/api/api"
import { getUrlId } from "@/utils/handle"

export default {
  setup() {
    const urlId = getUrlId().urlId

    const router = useRouter()
    let requireData = ref([])

    onBeforeMount(async () => {
      //getData
      requireData.value = await apiGetServiceInfo({ id: urlId })
      requireData.value = requireData.value.data
      // console.log(requireData.value)

      // languageInfo
      requireData.value.languageInfo = []
      requireData.value.language.forEach(item => {
        requireData.value.languageInfo.push(item.name)
      });
      // serviceInfo
      requireData.value.serviceInfo = []
      requireData.value.service.forEach(item => {
        requireData.value.serviceInfo.push(item.name)
      })
    })

    const deleteData = async () => {
      const result = await ToastConfirm("確定刪除此筆資料")
      if (result == true) {
          let msg = await apiDeleteServiceInfo({ id: urlId })
          if (msg.data == "刪除成功") {
            Toast(msg.data)
            router.push({ path: '/requirement/list' })
          } else {
            Toast(msg.message)
          }
      }
    }

    const goto = (val) => {
      router.push({ path: val })
    }

    return { requireData, goto, deleteData };
  },
  components: {
    Header
  },
};

</script>

<template>
  <Header title="資料內容" backToPath="/requirement/list" />
  <div class="requirementInfo">
    <section class="c-main" v-if="requireData.name">
      <div class="detail-container">
        <div class="item-container">
          <div class="item-title">{{ requireData.name }}</div>
          <div class="main1">
            <div class="title1">性別</div>
            <div class="content1">{{ requireData.gender == "Male" ? "男性" : (requireData.gender == "Female") ? "女性"
                :
                "不指定"
            }}</div>
          </div>
          <div class="main1">
            <div class="title1">年資</div>
            <div class="content1">{{ requireData.seniority.name }}</div>
          </div>
          <div class="main1">
            <div class="title1">語言</div>
            <div class="content1">{{ requireData.languageInfo.join('、') }}</div>
          </div>
          <div class="main2">
            <div class="title2">具備證照</div>
            <div class="content2" v-if="requireData.license?.name">{{ requireData.license.name }}</div>
            <div class="content2" v-if="!requireData.license?.name">無</div>
          </div>
        </div>
      </div>
      <div class="detail-container">
        <div class="item-container">
          <div class="item-title">服務需求</div>
          <div class="main2">
            <div class="content2">{{ requireData.serviceInfo.join('、') }}</div>
          </div>
        </div>
      </div>
      <div class="mt-4b btn-container">
        <div class="row">
          <div class="col">
            <button class="btn btn-outline-primary" type="button"
              @click="goto(`/requirement/edit/${requireData.id}`)">修改資料</button>
          </div>
          <div class="col">
            <button class="btn btn-outline-primary" type="button" @click="deleteData">刪除資料</button>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.requirementInfo {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.main1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 5px;
  margin-bottom: 5px;

  .title1 {
    font-size: 14px;
    font-weight: 400;
    color: #909090;
    margin-right: 14px;

  }

  .content1 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.main2 {
  margin-top: 10px;
  margin-bottom: 10px;

  .title2 {
    font-size: 14px;
    font-weight: 400;
    color: nth($secondary-color, 1);
  }

  .content2 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }

}
</style>